@Component({
  selector: 'app-connecting-user',
  templateUrl: './connecting-user.component.html',
  styleUrls: ['./connecting-user.component.css']
})
export class ConnectingUserComponent implements OnInit {

  constructor(private commonSrvc:CommonService, private connectingUserSrvc:ConnectingUserService) { }

  //service variables
    pageIndex:number = 3

  //table variables
    connectingUserList:ConnectingUserModel[]

    displayedColumns:string[] =[
      'game',
      'location',
      'blind',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'PlayerID',
      'ScreenName',
      'Deposit',
      'Withdraw',
      'Money',
      'Points',
      'TotalMoney',
      'UserProfit',
      'BettingAmount',
      'Rake',
      'OHORake',
      'HORake',
      'DRake',
      'SRake',
      'BlackListStatus',
      'IP',
      'ConnectingTime'
    ]

    tableSort:Sort = {active : 'TotalRake', direction : 'desc'}
  //search variables
    backLoading:boolean = true

  // MatPaginator
    // input
    // pageSizeOptions: number[] = environment.pageSizeOptions;

    // Output
    // pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //lifecycle hooks
    ngOnInit() {
      this.getListAndPageCount()
    }
    ngOnDestroy() {
      console.log('you leave connecting user')
    }

  //lifecycle hooks end


  getListAndPageCount(){
    // Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
    Promise.all([this.getSupportList()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getSupportList(){
    let promise = new Promise((resolve,reject) => {
      this.connectingUserSrvc.getConnectingUsers()
      .subscribe(
        (result :ConnectingUserModel[]) => {
          console.log(result)
          if(result.length > 0){
            
            //set artificial index
              for(let i = 0; i <= result.length - 1; i++){
                result[i].TotalMoney = result[i].Money + result[i].Points
                result[i].UserProfit = result[i].Deposit - result[i].Money 
              }
            //set artificial index end
  
            this.connectingUserList = result
            this.backLoading = false
          }else{

          }
          resolve()
        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  // getPageCount(){
  //   let promise = new Promise ((resolve,reject) => {
  //     this.commonSrvc.getPageCount(this.pageIndex)
  //     .subscribe(
  //       (result) => {
  //         this.pageEvent.length = result[0]['ID']
  //         resolve()
  //       },
  //         error => {
  //           console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
  //           console.log(error);
  //           reject()
  //         }
  //     )
  //   })
  //   return promise;
  // }


  searchList(event){
    

    event.preventDefault();
    let target = event.target;
                
    let column = target.querySelector('#column').value
    let value = (target.querySelector('#value').value) ? target.querySelector('#value').value : environment.ifSearchVariableEmpty

      this.commonSrvc.searchList(this.pageIndex,column,value)
      .subscribe(
        (result:ConnectingUserModel[]) => {
          console.log(result)
          //if there's result
          if(result.length > 0){
            this.connectingUserList = result
            console.log('results found')
          }
        },
        error => {
          if(error['statusText'] == 'Not Found'){
            //empty list
            this.connectingUserList = []
          }
        }
      )

  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }

  
  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    this.reload()
  }

}
