import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { UserAuthService } from '../../UserAuth/user-auth.service'
@Injectable({
  providedIn: 'root'
})
export class NotificationService {

  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }

  notifyAllRooms(title:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Notification/Update/NotificationType/Header/Location/AllRooms/Title/'+title)
    .pipe()
  }

}
