import { Component,Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
// services
import { CommonService } from '../../services/common/common.service'
import { UserCredentialsService } from '../../services/user-credentials.service'
import { SettingsService } from '../../services/settings/settings.service'
@Component({
    templateUrl: 'settings-dialog.html',
    styleUrls : ['settings-dialog.css']
  })

export class SettingsDialog {
    nightMode:boolean = false
    volume:number = 100
  constructor(
    public dialogRef: MatDialogRef<SettingsDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    // services
    private commonSrvc:CommonService,
    private userCredentialsSrvc:UserCredentialsService,
    private settingsSrvc:SettingsService) {
        this.settingsSrvc.isDarkTheme.next(this.nightMode)
  }

  close(): void {
    this.dialogRef.close();
  }

  logout(): void{
      this.close()
      this.userCredentialsSrvc.emitUserCredentials(null)
      this.commonSrvc.logout()
  }

  themeChanged():void{
    this.settingsSrvc.isDarkTheme.next(this.nightMode)
  }

  volumeChanged():void{
    this.settingsSrvc.mastervolume.next(this.volume)
    this.settingsSrvc.notifAudio()
  }

}