import { Component, OnInit } from '@angular/core';
import { NotificationService } from '../../../services/notice/notification/notification.service'
@Component({
  selector: 'app-in-gaming-table',
  templateUrl: './in-gaming-table.component.html',
  styleUrls: ['./in-gaming-table.component.css']
})
export class InGamingTableComponent implements OnInit {
  protected title:string

  constructor(private notificationSrvc:NotificationService) { }

  ngOnInit() {
  }

  notifyRoom(){
    this.notificationSrvc.notifyAllRooms(this.title)
      .subscribe(
        (res) =>{ 
          if(res['status'] == 200){
            alert('notice posted')
          }else{
            alert('something went wrong')
          }
        },
        err =>{
          alert('something went wrong' + err)
        }
      )
  }

}
