import { Component, OnInit, Inject, LOCALE_ID } from '@angular/core';
import { UserProfitService } from '../../../services/calculate/user-profit.service';
import { CommonService } from '../../../services/common/common.service'
import { FormsService } from '../../../services/utilities/forms.service'
import { environment } from 'src/environments/environment.prod';
import { NoResultBottomSheet2 } from '../../../utilities/NoResultBottomSheet/NoResultBottomSheet2'
import { interval, Subscription } from 'rxjs';
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import {PageEvent, Sort, MatBottomSheet} from '@angular/material';
import { formatDate } from '@angular/common';
interface userProfitModel{
  row_number:number,
  PlayerID:string,
  ShopID:string,
  DistributorID:string,
  HeadOfficeID:string,
  ScreenName:string,
  withdraw:number,
  withdrawTransfer:number,
  deposit:number,
  depositTransfer:number,
  BettingAmount:number,
  TotalRake:number,
  money:number,
  profit:number,
  profitMinusRake:number
}
@Component({
  selector: 'app-user-profit',
  templateUrl: './user-profit.component.html',
  styleUrls: ['./user-profit.component.css']
})
export class UserProfitComponent implements OnInit {

  constructor(private UserProfitSrvc:UserProfitService,
              private bottomsheet:MatBottomSheet, 
              private commonSrvc:CommonService,
              private formSrvc:FormsService,
              @Inject(LOCALE_ID) private locale: string) {

  }
  //table variables
    UserProfitList:userProfitModel[] = []

    displayedColumns:string[] =[  
      'row_number',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'PlayerID',
      'ScreenName',
      'deposit',
      'depositTransfer',
      'withdraw',
      'withdrawTransfer',
      'profit', // cannot be table sort because its value is calculated here in the front not in the server
      'BettingAmount',
      'TotalRake',
      'money',
      'profitMinusRake' // cannot be table sort because its value is calculated here in the front not in the server
    ]

    tableSort:Sort = {active : 'TotalRake', direction : 'desc'}

    // MatPaginator
      // input
      pageSizeOptions: number[] = environment.pageSizeOptions;

      // Output
      pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};
      
  //service variables
    pageIndex:number = 7

  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true

    timeopts:any[]

    //screean name value
      searchValue:string = 'Player31'
    // date
      startDate:string = '2018-12-26';
      endDate:string = '2019-12-26';

    // time
      startTime:string =' 00:00:00';
      endTime:string = ' 23:59:59';

  //lifecycle hooks
  ngOnInit() {
    this.timeopts = this.formSrvc.timeOptions
      this.getListAndPageCount();
  }
  ngOnDestroy() {
    console.log('you leave 1on1 support')
  }

//lifecycle hooks end

getListAndPageCount(){
  Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
    console.log('get list and page count successful');
    }, function(){ //if promise or promise2 fail
    console.log('something went wrong')
  })
}

getSupportList(){
  let promise = new Promise((resolve,reject) => {
    this.commonSrvc.getList3(this.pageIndex, 
                            this.pageEvent.pageIndex * this.pageEvent.pageSize, //ofset
                            this.pageEvent.pageSize, // limit
                            this.tableSort.active, // order by
                            this.tableSort.direction)
    .subscribe(
      (result :userProfitModel[]) => {

        console.log(result)
        //show No results found if 0 result else dont show
        if(result.length > 0){
          //set artificial index
            let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
            for(let i = 0; i <= result.length - 1; i++){
              row_number_new += 1;
              result[i].row_number = row_number_new;
              result[i].profit = result[i].deposit - result[i].withdraw;
              result[i].profitMinusRake = result[i].profit - result[i].TotalRake;
            }
          //set artificial index end
            this.UserProfitList = result
            this.backLoading = false
        }else{
          console.log('user profit no result')
        }
        resolve()
      },
      error => {
        console.log(error)
        reject()
      }
    )

  })

  return promise;
}

getPageCount(){
  let promise = new Promise ((resolve,reject) => {
    this.commonSrvc.getPageCount(this.pageIndex)
    .subscribe(
      (result) => {
        this.pageEvent.length = result[0]['ID']
        resolve()
      },
        error => {
          console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
          console.log(error);
          reject()
        }
    )
  })
  return promise;
}

  searchList(){
    this.showBackButton = true
    this.UserProfitList = []
    this.UserProfitSrvc.searchUserProfit( this.startDate + this.startTime ,this.endDate + this.endTime, this.searchValue )
      .subscribe(
        (result:userProfitModel[]) => {
          //if there's result
          if(result.length > 0){
            console.log(result)

              result[0].profit = result[0].deposit - result[0].withdraw;
              result[0].profitMinusRake = result[0].profit - result[0].TotalRake;

            this.UserProfitList = result
            this.backLoading = false
            console.log('results found')
          }else{
            console.log('search result not found')
          }
          // this.UserProfitList = result
        },
        error => {
          if(error['statusText'] == 'Not Found'){
            this.openSearchNoResultFound()

            //empty list
            this.UserProfitList = []
          }
        }
      )
  }

  //we call this function when the user interact with paginator or clicked the refresh button
  reload(){
    this.backLoading = true; 
    this.showBackButton = false;
    this.getListAndPageCount();
  }
  
  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    console.log(this.tableSort.direction)
    if(this.tableSort.direction){
      this.reload()
    }
  }

  dateValueChanged(event: MatDatepickerInputEvent<Date>){
    if(event.targetElement.id == 'from'){
      this.startDate = formatDate(event.value,'yyyy-MM-dd', this.locale).toString()
    }else if(event.targetElement.id == 'to'){
      this.endDate = formatDate(event.value, 'yyyy-MM-dd',this.locale).toString()
    }else{
      console.log('invalid target ID')
    }

  }

  openSearchNoResultFound(){
    this.bottomsheet.open(NoResultBottomSheet2,{
      data : {  value : this.searchValue, 
                column : '', 
                fromDate : this.startDate,
                fromTime : this.startTime,
                toDate : this.endDate,
                toTime : this.endTime 
              }
    })
      .afterDismissed().subscribe(
        () => { 
            console.log('dismissed');
            this.reload()
            this.searchValue = null;
          }
      ) 
  }

}
