import { Component, OnInit, Inject } from '@angular/core';
import { OfficeListService } from '../../../services/sales/office-list.service';
import { CommonService } from '../../../services/common/common.service';
import { UserCredentialsService } from '../../../services/user-credentials.service';
import { environment } from '../../../../environments/environment.prod';
import { UserModel } from '../../../interfaces/UserModel';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatBottomSheet, PageEvent} from '@angular/material';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';

interface officeModel{
  row_number: number,
  UserAccountID:string,
  OfficeID: string,
  Password: string,
  Name: string,
  RegisteredDateTime: string,
  PhoneNumber: string,
  TelephoneNumber: string,
  Commission: number,
  holdingMoney: number,
  TotalHoldingMoney: number,
  MembersMoney: number,
  TotalMoney: number,
  playersCount: number,
  shopsCount: number,
  distributorsCount: number,
  ParentID: string
}


@Component({
  selector: 'app-head-office-list',
  templateUrl: './head-office-list.component.html',
  styleUrls: ['./head-office-list.component.css']
})
export class HeadOfficeListComponent implements OnInit {
  addForm:FormGroup;
  constructor(private commonSrvc:CommonService, 
              private officeListSrvc:OfficeListService, 
              private userCredentialsSrvc:UserCredentialsService,
              public dialog: MatDialog,
              private bottomsheet:MatBottomSheet,
              fb:FormBuilder,) { 
    this.addForm = fb.group({
      Name: new FormControl('', Validators.required),
      PhoneNumber: new FormControl('', Validators.required),
      UserName: new FormControl('', Validators.required),
      Password: new FormControl('', Validators.required),
      Commission: new FormControl(this.addOfficeCommisionValue, Validators.required),
      ParentUserAccountID: ''
    });
  }

  //service variables
    // unlike the other components... pageIndex will depend on who the user is operatingheadoffice/headoffice/distributor/shop and it is editable through here
    protected pageIndex:number = 0
  //table variables
    hidePassword:boolean = true
    officeList :officeModel[] = []

    OfficeListColumns: string[] =  [
      'row_number',
      'OfficeID',
      'Name',
      'Commission',
      'holdingMoney',
      'TotalHoldingMoney',
      'MembersMoney',
      'TotalMoney',
      'playersCount',
      'shopsCount',
      'distributorsCount',
      'Lowrank',
      'Management'
    ];

    userCredentials:UserModel

    // MatPaginator
      //Input
      pageSizeOptions: number[] = environment.pageSizeOptions;
      //Output
      pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};
    
    // lowrank uprank variables
      // we use this if the user goes back
      maxUpstep:number
      commissionDefaultValue:number
      //commission's max value after user select its low rank and default is user's commission
      protected currentParentID:string 
      protected currentUserName:string 
      protected currentCommission:number

    //key to html elements
    protected key:boolean[] = [
      false,//distributor
      false,//headoffice
      false//operating
    ]
    
    protected parentIDHistory:string[] =[] 
    
    // add office variables
      addOfficeCommisionValue:number = 0

  //search variables
    backLoading:boolean = true

  //lifecycle hooks
    ngOnInit() {
      if(!this.userCredentials){
        console.log('requested... ')
        this.getUserCredentials()
      }
      
    }
    ngOnDestroy() {
      console.log('you leave office lists')
    }

  //lifecycle hooks end

  getListAndPageCount(){
    Promise.all([this.getOfficeList(),this.getPageCount()]).then(function() {
      // console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      // console.log('something went wrong')
    })
  }

  getOfficeList(){
    let promise = new Promise((resolve,reject) => {
      this.officeListSrvc.getList(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.currentParentID)
      .subscribe(
        (result:officeModel[]) => {
					console.log('​HeadOfficeListComponent -> getOfficeList -> result', result)
          
          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
            let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
            for(let i = 0; i <= result.length - 1; i++){
              row_number_new += 1;
              result[i].row_number = row_number_new;
              result[i].TotalMoney = result[i].TotalHoldingMoney + result[i].MembersMoney;
            }
            //set artificial index end
            this.officeList = result
            this.backLoading = false
            resolve()
          }else{
          }

        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.officeListSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }


  getUserCredentials(){
    this.userCredentialsSrvc.getPrivileges(localStorage.getItem(environment.tokenStorageKey))
      .subscribe(
         (res:UserModel) => {
           console.log("​HeadOfficeListComponent -> getUserCredentials -> res", res)
          //  console.log(res)
            this.userCredentials = res
            this.currentCommission = res.Commission
            this.currentParentID = res.UserAccountID
            this.currentUserName = res.UserName
            this.parentIDHistory.push(res.UserAccountID)
            let level;
            if(res.AccountType == "OperatingHeadOffice"){
              level = 3;
              this.maxUpstep = 2
              this.pageIndex = 2
            }else if(res.AccountType == "HeadOffice"){
              level = 2;
              this.maxUpstep = 1
              this.pageIndex = 1
            }else if(res.AccountType == "Distributor"){
              level = 1;
              this.maxUpstep = 0
              this.pageIndex = 0
            }else{
              // should be logged out because shop or edited account type is not allowed here
            }
            console.log('pageindex: ' + this.pageIndex)
            for(let i = 0; i < level; i++){
              this.key[i] = true
            }
             console.log(this.key)

            this.getListAndPageCount()
        }
      )
  }
  
  lowrank(office, lowrankCommission, UserName){
    // in case user force altered the pageIndex, the set it to 0 
    if(this.pageIndex != 0){

      this.parentIDHistory.push(this.currentParentID)


      this.pageIndex -= 1;
      this.currentParentID = office;
      this.currentUserName = UserName;
      this.currentCommission = lowrankCommission;
      this.addOfficeCommisionValue = 0;
      this.officeList = []
      this.reload()

    }else{
      this.pageIndex = 0
    }
  }

  uprank(){
    // this will keep pageindex to not step up to its limit.
    if(this.pageIndex <= 2 ){
      if(this.maxUpstep != this.pageIndex){
        this.pageIndex += 1;
        this.currentParentID = this.parentIDHistory.pop();
        this.officeList = []
        this.reload()
      }
    }else{
      console.log('pagindex overlapped')
    }
  }



  // check to all of user accounts
  officeIDCheck(){
    console.log(this.addForm.value.UserName)
    this.commonSrvc.checkIfUserNameExist(this.addForm.value.UserName)
      .subscribe(
        res => {
          if(res){
            this.addForm.controls['UserName'].setErrors({'invalid':true})
            alert('already exist')
          }else{
            alert('available')
          }
        }
      )
  }

  samplecheck(){
    // this.addForm.status
		console.log('​HeadOfficeListComponent -> samplecheck -> this.addForm.status', this.addForm.status)
    console.log('this.addOfficeCommisionValue', this.addOfficeCommisionValue)
    console.log('this.currentParentID', this.currentParentID)
  }
  addHeadOffice(){
    this.addForm.value.ParentUserAccountID = this.currentParentID

    if(this.addForm.status == 'VALID'){

      this.officeListSrvc.addOffice(this.addForm.value, this.pageIndex)
        .subscribe(
          (res:any) => {
            console.log('add office result 2 : '+ res )
  
            if(res['status']){
              alert('add office succesful')
              this.reload()
            }else{
              alert('adding office fail')
            }
  
          },
          err =>{
            if(err['status'] == 409){
              alert('username already exist')
            }else if(err['status'] == 400){
              alert('please fill up all fields....')
            }
            console.log('add office error : ' + err)
          }
        )
    }else{
      console.log('addform status : '+this.addForm.status)
      alert('please fill up the boxes correctly...')
    }
  }

  OpenOfficeOtherInfo(password, regDate, phoneNumber, officeName, officeID){
    const handHistoryDialog = this.dialog.open(OfficeInfoDialog, {
      width: '40%',
      data : {pass : password,
              registrationDate : regDate,
              contactno : phoneNumber,
              name : officeName,
              id : officeID}
    });

    handHistoryDialog.afterClosed().subscribe(
      () =>{
        console.log('handhistory closed')
      }
    )
  }

  //we call this function when the user interact with paginator 
  // or clicked the refresh button
  // or uprank and lowrank
  reload(){
    this.backLoading = true;
    this.getListAndPageCount()
  }

  openSearchNoResultFound(){
    this.bottomsheet.open(UserNameCheckSalesBottomSheet)
      .afterDismissed().subscribe(
        () => { 
            console.log('dismissed');
          }
      ) 
  }
}
@Component({
  selector: 'office-info-dialog',
  templateUrl: 'office-info-dialog.html',
})
export class OfficeInfoDialog {

  constructor(
    public dialogRef: MatDialogRef<OfficeInfoDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any) {}

  close(): void {
    this.dialogRef.close();
  }

}
@Component({
  selector: 'user-name-check-sales-bottom-sheet',
  templateUrl: 'user-name-check-bottom-sheet.html',
})
export class UserNameCheckSalesBottomSheet {

  constructor() {}

}