import { Component, OnInit } from '@angular/core';
import {CommonService} from '../../../services/common/common.service'
import { BlackListService } from '../../../services/user/blackList/black-list.service'
import { FormsService } from '../../../services/utilities/forms.service'
import { environment } from '../../../../environments/environment.prod';
import { BlacklistModel } from '../../../interfaces/BlacklistModel'
import { NoResultBottomSheet2 } from '../../../utilities/NoResultBottomSheet/NoResultBottomSheet2'
import { BlackListDialog } from '../../../utilities/BlackListDialog/BlackListDialog'
import {PageEvent, Sort, MatDialog, MatBottomSheet} from '@angular/material';

@Component({
  selector: 'app-black-list',
  templateUrl: './black-list.component.html',
  styleUrls: ['./black-list.component.css']
})

export class BlackListComponent implements OnInit {
  /* blacklist functions are not here it's in the dialog */
  constructor(private commonSrvc:CommonService,
              private blackListSrvc:BlackListService,
              private bottomsheet:MatBottomSheet,
              private formSrvc:FormsService,
              public dialog: MatDialog) { }

  //service variables
    pageIndex:number = 1
  //table variables
    blackList : BlacklistModel[] = []

    displayedColumns:string[] =[  
      'row_number',
      'HeadOfficeID',      
      'DistributorID',
      'ShopID',
      'UserAccountID',
      'ScreenName',
      'RegisteredDateTime',
      'ReleaseDate',
      'Reason',
      'Status'
    ]

    tableSort:Sort = {active : 'Status', direction : 'desc'}
    // MatPaginator
      // input
      pageSizeOptions: number[] = environment.pageSizeOptions;

      // Output
      pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true
    playeropts:any[]
    optionValue:string // value of playeroptions
    searchValue:string

  //lifecycle hooks
    ngOnInit() {
      this.playeropts = this.formSrvc.playerOnlySearchOptions
      this.getListAndPageCount()
    }

    ngOnDestroy() {
      console.log('you leave blacklist')
    }

  //lifecycle hooks end

  getListAndPageCount(){
    Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
        // this.backLoading = false
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getSupportList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.tableSort.active, this.tableSort.direction)
      .subscribe(
        (result :BlacklistModel[]) => {

          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
              let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
              for(let i = 0; i <= result.length - 1; i++){
                row_number_new += 1;
                result[i].row_number = row_number_new
              }
            //set artificial index end

            this.blackList = result
            this.backLoading = false
          }else{
            // this.searchResult = true
          }
          
          resolve()
        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }

  searchList(){
    this.commonSrvc.searchList(this.pageIndex,this.optionValue,this.searchValue)
    .subscribe(
      (result:BlacklistModel[]) => {
        //if there's result
        if(result.length > 0){
          this.blackList = result
          this.showBackButton = true
        }else{
          console.log('no results found')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          //stopping getting list for awhile
          let column = (this.optionValue == 'UserName') ? 'ID' : 'ScreenName'
          this.openSearchNoResultFound(column)
          //empty list
          this.blackList = []
        }
      }
    )
  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }

  releaseUser(UserAccountID:string){

    this.blackListSrvc.releaseUser(UserAccountID)
    .subscribe(
      result => {
        this.getListAndPageCount()
        console.log(result)
          if(result){
            alert('user released')
          }
      },
      error => {
        if(error['status'] == '404'){
          alert('something went wrong')
        }
      }
    )
          
  }

  //we call this function when the user interact with paginator or clicked the refresh button
  
  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }

  blacklistPlayer(){

    const handHistoryDialog = this.dialog.open(BlackListDialog, {
      width: '30%',
      data : {blacklistmode : [true,false,false]}
    });

    handHistoryDialog.afterClosed().subscribe(
      () =>{
        this.reload()
      }
    )
  }

  openBlacklist(value){
    const blacklistDialog = this.dialog.open(BlackListDialog, {
      width: '30%',
      data : {blacklistmode : [false,true,false], value : value}
    });

    blacklistDialog.afterClosed().subscribe(
      () =>{
        this.reload()
      }
    )
  }

  openSearchNoResultFound(columnName:string){
    this.bottomsheet.open(NoResultBottomSheet2,{
      data : [
        {value : this.searchValue, column : columnName, icon : 'person'} 
      ]
    })
      .afterDismissed().subscribe(
        () => {
            this.getListAndPageCount()
          }
      ) 
  }
}
