import { Injectable } from '@angular/core';
import { Http  } from '@angular/http';
import { DepositDailyModel } from '../../interfaces/DepositDailyModel'
import { WithdrawDailyModel } from '../../interfaces/WithdrawDailyModel'
import {UserAuthService } from '../UserAuth/user-auth.service'
import { map } from "rxjs/operators";
import { Subject } from 'rxjs/Rx';


@Injectable({
  providedIn: 'root'
})
export class DashboardService {
    //observables
    withdrawToday = new Subject<WithdrawDailyModel[]>();
    depositToday = new Subject<DepositDailyModel[]>();

    totalMemberObservable = new Subject<string>();
    totalRegisteredTodayObservable = new Subject<string>();
    connectingMembersObservable = new Subject<string>();
    
    totalWithdrawTodayObservable = new Subject<string>();
    totalDepositTodayObservable = new Subject<string>();
    
  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }


  emitTotalMember(val){
    this.totalMemberObservable.next(val)
  }

  emitTotaRegisteredToday(val){
    this.totalRegisteredTodayObservable.next(val)
  }

  emitConnectingMembers(val){
    this.connectingMembersObservable.next(val)
  }

  emitWithrawToday(val:WithdrawDailyModel[]){
    this.withdrawToday.next(val)
  }

  emitDepositToday(val:DepositDailyModel[]){
    this.depositToday.next(val)
  }


  getTotalRegisteredUser(){

    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/TotalRegisteredUser', this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {}
      )
    )
  }

  getTotalRegisteredUserToday(){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/TotalRegisteredUsersToday', this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {}
      )
    )
  }

  getDepositWithdrawToday(){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/TotalTransactionRecent/', this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {}
      )
    )
  }

  getConnectingMembers(){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/OnlineStatuses/', this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {}
      )
    )
  }

  //charts
    getBettingRankingDaily(){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/HeadOffice/Betting/Daily', this.userAuthSrvc.httpOption)
      .pipe(
        map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} 
        )
      )
    }

    getBettingRankingWeekly(){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/HeadOffice/Betting/Weekly', this.userAuthSrvc.httpOption)
      .pipe(
        map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} 
        )
      )
    }

    getBettingRankingMonthly(){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/HeadOffice/Betting/Monthly', this.userAuthSrvc.httpOption)
      .pipe(
        map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} 
        )
      )
    }

    getDepositRankingDaily(){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/HeadOffice/Deposit/Daily/', this.userAuthSrvc.httpOption)
      .pipe(
        map(res => 
          {
            return res.arrayBuffer().byteLength > 0 ? res.json() : {}
          }
        )
      )
    }

    getWithdrawRankingDaily(){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Dashboard/HeadOffice/Withdraw/Daily/', this.userAuthSrvc.httpOption)
      .pipe(
        map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {}
        )
      )
    }
  //charts end

  
}
