import { Component, OnInit, Inject, LOCALE_ID } from '@angular/core';
import { CommonService } from '../../../services/common/common.service'
import { environment } from '../../../../environments/environment.prod';
import {PageEvent, Sort} from '@angular/material';
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import { FormsService } from '../../../services/utilities/forms.service'
import { formatDate } from '@angular/common';
import { MoneytransactionsService } from '../../../services/moneytransactions/moneytransactions.service'
interface withdrawModel{
  ID:number
  UserTransactionID:string
  UserAccountID:string
  HeadOfficeID:string
  DistributorID:string
  ShopID:string
  PlayerID:string
  ScreenName:string
  Name:string
  ContactNumber:string
  ExistingAmount:string
  ApplicationAmount:string
  RemainingAmount:string
  TransactionStatus:string
  RequestedDateTime:string

  BankName:string
  AccountNumber:string
  AccountHolder:string

}
@Component({
  selector: 'app-withdraw-management',
  templateUrl: './withdraw-management.component.html',
  styleUrls: ['./withdraw-management.component.css']
})
export class WithdrawManagementComponent implements OnInit {
  constructor(private commonSrvc:CommonService, 
              private formSrvc:FormsService,
              private moneyTransactionsSrvc:MoneytransactionsService,
              @Inject(LOCALE_ID) private locale: string) { }

  //service variables
    pageIndex:number = 6
  //table variables
    withdrawList:withdrawModel[]

    displayedColumns:string[] =[  
      'ID',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'PlayerID',
      'ScreenName',
      'BankName',
      'AccountNumber',
      'Name',
      'ContactNumber',
      'ExistingAmount',
      'ApplicationAmount',
      'RemainingAmount',
      'TransactionStatus',
      'Management',
      'RequestedDateTime'
    ]

    tableSort:Sort = {active : 'TransactionStatus', direction : 'desc'}

    // MatPaginator
      // input
      pageSizeOptions: number[] = environment.pageSizeOptions;

      // Output
      pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};
      
  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true
    protected continuousHTTPRequest:boolean = true

    timeopts:any[]

    //screean name value
      searchValue:string = 'player1'
    // date
      startDate:string = '2018-12-26';
      endDate:string = '2019-12-26';

    // time
      startTime:string =' 00:00:00';
      endTime:string = ' 23:59:59';
  //on component variables
    currentManageButton:number
    hideManageButton:boolean = true


  //lifecycle hooks
  ngOnInit() {
    this.timeopts = this.formSrvc.timeOptions
    this.getListAndPageCount()
  }
  ngOnDestroy() {
    console.log('you leave withdraw list')
  }

//lifecycle hooks end

getListAndPageCount(){
  Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
    console.log('get list and page count successful');
    
    }, function(){ //if promise or promise2 fail
    console.log('something went wrong')
  })
}

getSupportList(){
  let promise = new Promise((resolve,reject) => {
    this.commonSrvc.getList3( this.pageIndex, 
                              this.pageEvent.pageIndex * this.pageEvent.pageSize, //ofset
                              this.pageEvent.pageSize, // limit
                              this.tableSort.active, // order by
                              this.tableSort.direction)
    .subscribe(
      (result :withdrawModel[]) => {
        console.log(result)
        if(result.length > 0){
          //set artificial index
          let row_number_new = 0
          for(let i = 0; i <= result.length - 1; i++){
            row_number_new += 1;
            result[i].ID = row_number_new
          }

          this.withdrawList = result
          this.backLoading = false
        }else{
          console.log('no result')
        }

        resolve()
      },
      error => {
        console.log(error)
        reject()
      }
    )

  })

  return promise;
}

getPageCount(){
  let promise = new Promise ((resolve,reject) => {
    this.commonSrvc.getPageCount(this.pageIndex)
    .subscribe(
      (result) => {
        this.pageEvent.length = result[0]['ID']
        resolve()
      },
        error => {
          console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
          console.log(error);
          reject()
        }
    )
  })
  return promise;
}

  searchList(event){
    event.preventDefault();
    let target = event.target;
                
    let column = target.querySelector('#column').value
    let value = (target.querySelector('#value').value) ? target.querySelector('#value').value : environment.ifSearchVariableEmpty

    this.commonSrvc.searchList(this.pageIndex,column,value)
    .subscribe(
      (result:withdrawModel[]) => {
        if(result.length > 0){
          
          this.withdrawList = result
          console.log('results found')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          //empty list
          this.withdrawList = []
        }
      }
    )
  }

  approvedWithdraw(UserTransactionID, UserAccountID){
    this.hideManageButton = false

    this.moneyTransactionsSrvc.approveWithdraw(UserTransactionID,UserAccountID)
      .subscribe(
        (res) => {
          console.log(res)
          this.hideManageButton = true
          this.reload()
        }
      )
  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()

  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }

  dateValueChanged(event: MatDatepickerInputEvent<Date>){
    if(event.targetElement.id == 'from'){
      this.startDate = formatDate(event.value,'yyyy-MM-dd', this.locale).toString()
    }else if(event.targetElement.id == 'to'){
      this.endDate = formatDate(event.value, 'yyyy-MM-dd',this.locale).toString()
    }else{
      console.log('invalid target ID')
    }

  }


}
