import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from "rxjs/operators";
import { environment } from '../../../../environments/environment.prod';
import { UserAuthService } from '../../UserAuth/user-auth.service'
@Injectable({
  providedIn: 'root'
})
export class SupportService {

  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }

  getWriteNotice(userID:string, supportID:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/OneOnOne/UserAccountID/'+userID+'/SupportTicketID/'+supportID)
    .pipe(
      map(res => res.json())
      )
  }

  answerSupport(SupportTicketID:string,UserAccountID:string, Answer:string ){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/OneOnOne/SupportTicketID/'+SupportTicketID+'/UserAccountID/'+UserAccountID+'/Answer/'+Answer, this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.json())
      )
  }
  
}
