import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {
  isDarkTheme = new Subject<boolean>()
  mastervolume = new Subject<number>()
  //notification sfx
    notificationAudio = new Audio()
    volume:number = 1
  constructor() {
    this.mastervolume.subscribe(
      (res) => {this.volume = res / 100;},
      err => console.log(err)
    )
   }

  notifAudio(){

    this.notificationAudio.src = '../../../assets/notification.mp3'
    this.notificationAudio.load();
    this.notificationAudio.volume = this.volume;
    this.notificationAudio.play();
  }

  toggleMute(toggle:boolean){

    if (toggle) {
      this.notificationAudio.volume = 1;
    } else {
      this.notificationAudio.volume = 0;
    }
  }


}
