import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { map } from "rxjs/operators";
import { environment } from '../../../environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class UserAuthService {
  private authenticated:boolean = false

  private httpOptions:any
  public server:string = 'http://192.168.254.109:8080'

  constructor(private http:Http) {
    // environment.production 
    //   ? this.server = 'https://tester-holdem-server.4b63.pro-ap-southeast-2.openshiftapps.com' 
    //   : this.server = 'http://192.168.254.101:8080';
  }

  get isAuthenticated(){
    return this.authenticated
  }

  setLoggedIn(value:boolean){
    this.authenticated = value
  }

  storeToken(token:string){
    localStorage.setItem(environment.tokenStorageKey, token)
    this.authenticated = true
  }

  login(UserName, Password){
    //post these details to API return token if correct credentials
    console.log('sent')
    return this.http.post(this.server+'/Api/v1/Admin/Login',{
      UserName,
      Password
    })      
    .pipe(
      map(res => {
        return res.json()
      })
    )
  }


  get httpOption(){
    this.httpOptions = {
      headers: new Headers({
        'Content-Type':  'application/json',
        'Authorization': `Bearer ${localStorage.getItem(environment.tokenStorageKey)}`
      })
    };

    return this.httpOptions;
  }

}
