@Component({
  selector: 'app-deposit-management',
  templateUrl: './deposit-management.component.html',
  styleUrls: ['./deposit-management.component.css']
})
export class DepositManagementComponent implements OnInit {
  // remote port is 8080
  ws = new WebSocket("ws://localhost:3000/?UserAccountID=d31a6484-5249-4faa-b7d3-aa6f93419d8c");
  // private ws = new WebSocket("ws://httpsserver-socket-server-socket.4b63.pro-ap-southeast-2.openshiftapps.com/?UserAccountID=1571028c-eec2-47ac-a1bf-0a62086551d2");
  // private ws = new WebSocket('wss://'+this.userAuthSrvc.server.slice(this.userAuthSrvc.server.search('/') + 2 , this.userAuthSrvc.server.length )+'/?UserAccountID=cdf61833-66e1-4a92-a46c-0782ed77c203')

  constructor(private commonSrvc:CommonService, 
              private moneyTransactionsSrvc:MoneytransactionsService,
              private formSrvc:FormsService,
              private excel:ExcelService,
              @Inject(LOCALE_ID) private locale: string) {

   }

  //service variables
    pageIndex:number = 0
  //table variables
    depositList:DepositModel[]

    displayedColumns:string[] =[  
      'ID',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'UserName',
      'ScreenName',
      'Name',
      'PhoneNumber',
      'Amount',
      'TransactionStatus',
      'Management',
      'RequestedDateTime',
      'ApprovedDateTime'
    ]

    tableSort:Sort = {active : 'TransactionStatus', direction : 'desc'}

    // MatPaginator
      // input
      pageSizeOptions: number[] = environment.pageSizeOptions;

      // Output
      pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true

    timeopts:any[]

    //screean name value
      searchValue:string = 'player1'
    // date
      startDate:string = '2018-12-26';
      endDate:string = '2019-12-26';

    // time
      startTime:string =' 00:00:00';
      endTime:string = ' 23:59:59';
  // table variables
    currentManageButton:number
    hideManageButton:boolean = true
  //lifecycle hooks
    ngOnInit() {

      this.timeopts = this.formSrvc.timeOptions
      // this.ws.on()
      this.ws.onopen = function(openSocket){
        console.log(openSocket.type)
      }
      
      this.ws.onmessage = function(event){
        var received_msg = event.data;
          console.log(JSON.parse(received_msg));
      }

      this.getListAndPageCount()

    }
    ngOnDestroy() {
      this.ws.close()
      console.log('you leave deposit list')
    }

  //lifecycle hooks end

  AcceptDepositWebSocket(receiver,amount,depositID){

    if ("WebSocket" in window) {

      var MessageReceiver = receiver;
      var Amount ="";//for pop up message in game only
      var DepositUUID = depositID;
      var DepositNotice = "Deposit Approved Amount "+amount;
      var message = "{\"Type\":\"NotifyPlayerDeposit\",\"MessageReceiver\" :\"" + MessageReceiver +
          "\",\"DepositNotice\":\"" + DepositNotice + "\",\"DepositUUID\":\"" + DepositUUID + "\"}";
          console.log("Notify Player");
          
      this.ws.send(message);

  } else {
      alert("WebSocket NOT supported by your Browser!");
  }
    // this.ws.o
    // if("WebSocket" in window){
    //   var DepositNotice = "Deposit Approved Amount "+amount;
    //   var message = "{\"Type\":\"NotifyPlayerDeposit\",\"MessageReceiver\" :\"" + receiver +
    //       "\",\"DepositNotice\":\"" + DepositNotice + "\",\"DepositUUID\":\"" + depositID + "\"}";
    //   this.ws.send(message)
    // }else{
    //   alert('your web browser doesnt support websocket')
    // }
  }

  getListAndPageCount(){
    Promise.all([this.getDepositList()]).then(function() {
    // Promise.all([this.getDepositList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getDepositList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3( this.pageIndex, 
                                this.pageEvent.pageIndex * this.pageEvent.pageSize, //ofset
                                this.pageEvent.pageSize, // limit
                                this.tableSort.active, // order by
                                this.tableSort.direction)
      .subscribe(
        (result :DepositModel[]) => {
          console.log(result)
          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
            let row_number_new = 0
            for(let i = 0; i <= result.length - 1; i++){
              row_number_new += 1;
              result[i].ID = row_number_new
            }

            this.depositList = result
            this.backLoading = false
          }else{
          }

          resolve()
        },
        error => {
          console.log('deposit error ' + error)
          console.log('get support list error')
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }

  searchList(){
    this.showBackButton = true
    this.depositList = []
    this.moneyTransactionsSrvc.searchDeposit(this.startDate + this.startTime ,this.endDate + this.endTime, this.searchValue)
      .subscribe(
        (result:DepositModel[]) => {
          console.log(result)
          if(result.length > 0){
            this.depositList = result
            console.log('results found')
          }else{
            console.log('no results found')
          }
        },
        error => {
          console.log('this is the error --> '+ error['status'])
          // console.log()
          if(error['status'] == 404){

            //empty list
            this.depositList = []
          }
        }
      )
  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }
  
  updateDeposit(UserTransactionID:string){
    this.hideManageButton = false
    if(UserTransactionID){
      this.moneyTransactionsSrvc.archivedDeposit(UserTransactionID)
      .subscribe( 
        result => {
          console.log(result)
          this.hideManageButton = true
          alert('delete successful')
          }
        )
    }
  }

  approveDeposit(UserTransactionID:string, UserAccountID:string, Amount){
    this.hideManageButton = false
    this.moneyTransactionsSrvc.approveDeposit(UserTransactionID, UserAccountID)
    .subscribe( 
      result => {
        this.reload()
        if(result.toString() == 'true'){
          this.AcceptDepositWebSocket(UserAccountID, Amount, UserTransactionID)
          // alert('approved successfully')
        }
        
        if(result['AlreadyApproved'] == true){
          alert('already approved')
          this.currentManageButton = 0
        }
        
        this.hideManageButton = true
      },
      error => {
        console.log(error)
        alert('something went wrong')
      }
      )
  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }

  dateValueChanged(event: MatDatepickerInputEvent<Date>){
    if(event.targetElement.id == 'from'){
      this.startDate = formatDate(event.value,'yyyy-MM-dd', this.locale).toString()
    }else if(event.targetElement.id == 'to'){
      this.endDate = formatDate(event.value, 'yyyy-MM-dd',this.locale).toString()
    }else{
      console.log('invalid target ID')
    }

  }

  WebSocketTest() {

    if ("WebSocket" in window) {
       // alert("WebSocket is supported by your Browser!");
        // Let us open a web socket

        this.ws.onopen = function () {
            // Web Socket is connected, send data using send()
           // ws.send("Message to send");
          //  alert("Message is sent...");
        };
        this.ws.onmessage = function (evt) {
            var received_msg = evt.data;
            console.log(received_msg);
        };
        this.ws.onclose = function () {
            // websocket is closed.
           // alert("Connection is closed...");
        };
    } else {

        // The browser doesn't support WebSocket
        alert("WebSocket NOT supported by your Browser!");
    }
  }

  sampleexcel():void{
    this.excel.exportAsExcelFile(this.depositList, 'sample')
  }
}
