import { Component, OnInit } from '@angular/core';
import {SupportService} from '../../../services/notice/support/support.service'
import {CommonService} from '../../../services/common/common.service'
import { environment } from '../../../../environments/environment.prod';
import { SupportModel } from '../../../interfaces/SupportModel'
import { OptionModel } from '../../../interfaces/OptionModel'
import {PageEvent, Sort, MatDialog, MatBottomSheet} from '@angular/material';
import {AnswerSupportDialog} from '../../../utilities/AnswerSupportDialog/AnswerSupportDialog'
import { NoResultBottomSheet2 } from '../../../utilities/NoResultBottomSheet/NoResultBottomSheet2'

@Component({
  selector: 'app-one-on-one-support',
  templateUrl: './one-on-one-support.component.html',
  styleUrls: ['./one-on-one-support.component.css']
})
export class OneOnOneSupportComponent implements OnInit {

  constructor(private supportSrvc:SupportService, 
              private commonSrvc:CommonService,
              private bottomsheet:MatBottomSheet,
              public dialog: MatDialog){ }

  //service variables
    pageIndex:number = 4

  //table variables
    supportList:SupportModel[]

    displayedColumns:string[] =[  
      'row_number',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'PlayerID',
      'ScreenName',
      'Title',
      'Status',
      'Management',
      'RegisteredDateTime',
      'AnswerDateTime'
    ]

    tableSort:Sort = {active : 'Status', direction : 'desc'}

  // MatPaginator
    // input
    pageSizeOptions: number[] = environment.pageSizeOptions;

    // Output
    pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true

    optionValue:string = 'PlayerID'
    searchValue:string

    columnOptions:OptionModel[]= [
      {value : 'PlayerID', name : 'User ID'},
      {value : 'ScreenName', name : 'Screen Name'},
      {value : 'Title', name : 'Title'},
      {value : 'Reason', name : 'Contents'}
    ]

  //lifecycle hooks
    ngOnInit() {
      this.getListAndPageCount()
    }

    ngOnDestroy() {
      console.log('you leave 1on1 support')
    }

  //lifecycle hooks end

  getListAndPageCount(){
    Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getSupportList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.tableSort.active, this.tableSort.direction)
      .subscribe(
        (result :SupportModel[]) => {
          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
            let row_number_new = 0
            for(let i = 0; i <= result.length - 1; i++){
              row_number_new += 1;
              result[i].row_number = row_number_new
            }
            this.supportList = result
            this.backLoading = false
          }else{
            console.log('result not found')
          }
          resolve()
        },
        error => {
          if(error['status'] == 403){
            this.commonSrvc.logout()
          }
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }

  // will activate after the answer button in table is clicked
  getWriteNotice(userID:string, supportID:string){
    this.supportSrvc.getWriteNotice(userID,supportID)
    .subscribe(
      (result) => {
        if(result){
          this.AnswerSupport(result[0])
        }
       },
     error => {
         console.log(error)
       }
     )
  }


  searchList(){
    this.commonSrvc.searchList(this.pageIndex,this.optionValue,this.searchValue)
    .subscribe(
      (result:SupportModel[]) => {
        console.log(result)
        //if there's result
        if(result.length > 0){
          this.supportList = result
          console.log('results found')
        }else{
          console.log('no results found')
        }
        this.showBackButton = true
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          let icon = '';
          switch(this.optionValue) { 
            case "PlayerID": { 
               icon = 'credit_card';
               break; 
            } 
            case "ScreenName": { 
               icon = 'person';
               break; 
            } 
            case "Title": {
               icon = 'title' ;
               break;    
            } 
            default: { 
              icon = 'code'; 
               break;              
            } 
         }
          //empty list
          this.supportList = []
          this.openSearchNoResultFound(this.optionValue, icon)
        }else{
          this.commonSrvc.logout()
        }
      }
    )
  }


  //we call this function when the user interact with paginator or clicked the refresh button
  reload(){
    this.backLoading = true; 
    this.getListAndPageCount()
  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()

    }
  }

  AnswerSupport(writenotice){
    const handHistoryDialog = this.dialog.open(AnswerSupportDialog, {
      width: '30%',
      data : writenotice
    });

    handHistoryDialog.afterClosed().subscribe(
      () =>{
        this.getListAndPageCount()
      }
    )
  }

  openSearchNoResultFound(columnName:string, icon:string){
    this.bottomsheet.open(NoResultBottomSheet2,{
      data : [
        {value : this.searchValue, column : columnName, icon : icon} 
      ]
    })
      .afterDismissed().subscribe(
        () => {
            this.getListAndPageCount()
          }
      ) 
  }
  
}
