import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from "rxjs/operators";
import {UserAuthService} from '../UserAuth/user-auth.service'
@Injectable({
  providedIn: 'root'
})
export class OfficeListService {

  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }

    //http request variables
    page:string[] = [
      'Shop', //0
      'Distributor', //1
      'HeadOffice' //2
    ]

  getList(pageIndex:number, offset:number, limit:number, ParentID:string ){

      return this.http.get(this.userAuthSrvc.server + '/Api/v1/Sales/'+this.page[pageIndex]+'List'+'/ParentID/'+ParentID+'/Limit/'+limit+'/Offset/'+offset)
      // return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/ParentID/'+ParentID+'/Limit/'+environment.resultLimit+'/Offset/'+offset+'/', this.userAuthSrvc.httpOption)
      .pipe(
        map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
  }

  getPageCount(pageIndex:number){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Sales/Pagination/Page/'+pageIndex, this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }

  // check to all of user accounts
  checkIfHeadOfficeExist(username){
    try{
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/UserNameCheck/'+username)
      .pipe(
        map( res => res.json() )
      )
    }catch(error){
      console.log('http' + error)
    }
  }
  
  addOffice(addHeadOfficeVariables, pageIndex:number){
      return this.http.post(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Add/',addHeadOfficeVariables);
  }

}
