import { Component, OnInit, Inject } from '@angular/core';
import {CommonService} from '../../../services/common/common.service'
import { environment } from '../../../../environments/environment.prod';
import { OptionModel } from '../../../interfaces/OptionModel'
import {PageEvent, MatDialog, MatDialogRef, MAT_DIALOG_DATA, Sort} from '@angular/material';
import {FormBuilder, FormGroup} from '@angular/forms';
import {BlackListDialog} from '../../../utilities/BlackListDialog/BlackListDialog'
interface memberListModel{
  row_number:number,
  HeadOfficeID: string,
  DistributorID: string,
  ShopID: string,
  PlayerID: string,
  ScreenName: string
  BlackListStatus: string,
  CurrentPoints: number,
  TotalPoints: number,
  LastLogin: string,
  Money: number,
  OnlineStatus: string,
  PhoneNumber: string,
  RegisteredDateTime: string
}

@Component({
  selector: 'app-member-list',
  templateUrl: './member-list.component.html',
  styleUrls: ['./member-list.component.css']
})
export class MemberListComponent implements OnInit {
  constructor(private commonSrvc:CommonService,
              public dialog: MatDialog,
              fb:FormBuilder,) {

    this.searchForm = fb.group({
      searchValue: null,
      column: '0'
    });

  }

  //service variables
    pageIndex:number = 5
  //table variables
    memberList:memberListModel[]

    displayedColumns:string[] =[  
      'row_number',
      'HeadOfficeID',
      'DistributorID',
      'ShopID',
      'PlayerID',
      'ScreenName'  ,
      'Money',
      'CurrentPoints',
      'TotalPoints',
      'OnlineStatus',
      'LastLogin',
      'BlackListStatus',
      'Management'
    ]

    tableSort:Sort = {active : 'LastLogin', direction : 'desc'}
  //angular material search variables
    searchForm:FormGroup;

    columnOptions:OptionModel[]= [
      {value : '0', name : 'User ID'},
      {value : '1', name : 'Screen Name'},
      {value : '2', name : 'Store ID'}
    ]
  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true

  // MatPaginator
    // input
    pageSizeOptions: number[] = environment.pageSizeOptions;

    // Output
    pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //lifecycle hooks
    ngOnInit() {
      this.getListAndPageCount()
    }
    ngOnDestroy() {
      console.log('you leave member list')
    }
  //lifecycle hooks end


  getListAndPageCount(){
    Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getSupportList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.tableSort.active, this.tableSort.direction)
      .subscribe(
        (result :memberListModel[]) => {
          //set artificial index
          if(result.length > 0){
            let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
            for(let i = 0; i <= result.length - 1; i++){
              row_number_new += 1;
              result[i].row_number = row_number_new
            }
            
            this.memberList = result
            this.backLoading = false
          }else{
          //show No results found if 0 result else dont show
          }
          resolve()
        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }


  searchList(){

    this.commonSrvc.searchList(this.pageIndex,this.searchForm.value.column,this.searchForm.value.searchValue)
    .subscribe(
      (result:memberListModel[]) => {
        console.log(result)
        //if there's result
        if(result.length > 0){
          this.memberList = result

          console.log('results found')
        }else{
          console.log('member list search no result')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){

          //empty list
          this.memberList = []
        }
      }
    )
    this.showBackButton = true
  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }


  OpenMemberOtherInfo(regDate, phoneNumber, sname, username){
    const handHistoryDialog = this.dialog.open(MemberInfoDialog, {
      width: '40%',
      data : {
              registrationDate : regDate,
              contactno : phoneNumber,
              screenname : sname,
              id : username}
    });

    handHistoryDialog.afterClosed().subscribe(
      () =>{
        console.log('handhistory closed')
      }
    )
  }

  openBlacklist(value){
    const blacklistDialog = this.dialog.open(BlackListDialog, {
      width: '30%',
      data : {blacklistmode : [false,true,false], value : value}
    });

    blacklistDialog.afterClosed().subscribe(
      () =>{
        this.reload()
      }
    )
  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }
  

}

@Component({
  selector: 'member-info-dialog',
  templateUrl: 'member-info-dialog.html',
})
export class MemberInfoDialog {

  constructor(
    public dialogRef: MatDialogRef<MemberInfoDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any) {}

  close(): void {
    this.dialogRef.close();
  }

}