import { Component,Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {SupportService} from '../../services/notice/support/support.service'
import { WriteNoticeModel } from '../../interfaces/WriteNoticeModel'
@Component({
    templateUrl: 'answer-support-dialog.html',
    styleUrls : ['answer-support.css']
  })

export class AnswerSupportDialog {
  protected answer:string
  constructor(private supportSrvc:SupportService,
              public dialogRef: MatDialogRef<AnswerSupportDialog>,
              @Inject(MAT_DIALOG_DATA) public data: WriteNoticeModel) {
    this.answer = data.Answer
  }

  answerSupport(){
    this.supportSrvc.answerSupport(this.data.SupportTicketID, this.data.UserAccountID, this.answer)
    .subscribe(
      (result) => {
       alert('answered successful')
        // console.log(result)
       },
     error => {
         console.log(error)
       }
     )
  }

  close(): void {
    this.dialogRef.close();
  }


  cancel(){
    this.dialogRef.close();

    this.dialogRef.afterClosed().subscribe(
      ()=>{
        console.log('cancelled')
      }
    )
  }

}