import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from "rxjs/operators";
import { UserAuthService } from '../../../services/UserAuth/user-auth.service'
@Injectable({
  providedIn: 'root'
})
export class GameLogService {

  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }

  getHandHistory(seasonID:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/HandHistory/SeasonID/'+seasonID, this.userAuthSrvc.httpOption)
      .pipe(
        map(res => { return res.json();})
      )
  }
}
