//defaults
  import { BrowserModule } from '@angular/platform-browser';
  import { NgModule, enableProdMode } from '@angular/core';
  import { AppComponent } from './app.component';

//modules
  import { FormsModule, ReactiveFormsModule  } from '@angular/forms';
  import {RouterModule, Routes} from '@angular/router'
  import { HttpModule } from '@angular/http';
  import { ChartsModule } from 'ng2-charts';
  // angular material
  import {  MatSliderModule ,
            MatToolbarModule,
            MatIconModule,
            MatButtonModule,
            MatExpansionModule,
            MatListModule,
            MatGridListModule,
            MatCardModule,
            MatProgressBarModule,
            MatTableModule,
            MatTooltipModule,
            MatDialogModule,
            MatSlideToggleModule,
            MatPaginatorModule,
            MatProgressSpinnerModule,
            MatFormFieldModule,
            MatInputModule,
            MatSelectModule,
            MatBottomSheetModule,
            MatDatepickerModule,
            MatNativeDateModule,
            MatChipsModule,
            MatSortModule,
            MatBadgeModule } from '@angular/material';

  import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
  import { MatSidenavModule } from '@angular/material/sidenav';
//components
  //sales
  import { HeadOfficeListComponent,OfficeInfoDialog,UserNameCheckSalesBottomSheet } from './components/sales/head-office-list/head-office-list.component';
  import { MemberListComponent, MemberInfoDialog } from './components/sales/member-list/member-list.component';
  //calculate
  import { CalculateManageComponent } from './components/calculate/calculate-manage/calculate-manage.component';
  import { UserProfitComponent } from './components/calculate/user-profit/user-profit.component';
  //user
  import { ConnectingUserComponent } from './components/user/connecting-user/connecting-user.component';
  import { BlackListComponent } from './components/user/black-list/black-list.component';
  import { IPinquireComponent } from './components/user/ipinquire/ipinquire.component';
  //deposit_withdraw
  import { DepositManagementComponent } from './components/deposit_withdraw/deposit-management/deposit-management.component';
  import { WithdrawManagementComponent } from './components/deposit_withdraw/withdraw-management/withdraw-management.component';
  //notice
  import { LobbyNoticeComponent } from './components/notice/lobby-notice/lobby-notice.component';
  import { InGamingTableComponent } from './components/notice/in-gaming-table/in-gaming-table.component';
  import { OneOnOneSupportComponent } from './components/notice/one-on-one-support/one-on-one-support.component';
  //log
  import { GameLogComponent,NoResultBottomSheet,HandHistoryDialog } from './components/log/game-log/game-log.component';
  import { JackpotLogComponent } from './components/log/jackpot-log/jackpot-log.component';
  import { TransferLogComponent } from './components/log/transfer-log/transfer-log.component';
  import { PointLogComponent } from './components/log/point-log/point-log.component'
  //dashboard
  import { DashboardComponent } from './components/dashboard/dashboard.component';
  //login
  import { LoginComponent } from './components/login/login.component';
  //master page
  import { MasterPageComponent } from './components/master-page/master-page.component';
//custom directives  
  import { TranslateDirective } from './directives/translate.directive';

//services
  import {CommonService} from '../app/services/common/common.service';
  import {UserAuthGuard} from '../app/services/UserAuth/user-auth.guard';

//utility all over components and it is independent...
  import { NoResultBottomSheet2 } from './utilities/NoResultBottomSheet/NoResultBottomSheet2'
  import { BlackListDialog } from './utilities/BlackListDialog/BlackListDialog'
  import { AnswerSupportDialog } from './utilities/AnswerSupportDialog/AnswerSupportDialog'
  import { SettingsDialog } from './utilities/SettingsDialog/SettingsDialog'
  
  const routes: Routes = [
    
      { path: 'admin', 
        component: MasterPageComponent,
        canActivate : [UserAuthGuard],
        children: [
          //sales
            {path: 'headOfficeList', component: HeadOfficeListComponent},
            {path: 'memberList', component: MemberListComponent},
          //calculate
            {path: 'calculateManage', component: CalculateManageComponent},
            {path: 'userProfit', component: UserProfitComponent},
          //user
            {path: 'connectingUser', component: ConnectingUserComponent},
            {path: 'blackList', component: BlackListComponent},
            {path: 'IPinquire', component: IPinquireComponent},
          //deposit_withdraw
            {path: 'depositManagement', component: DepositManagementComponent},
            {path: 'withdrawManagement', component: WithdrawManagementComponent},
          //notice
            {path: 'lobbyNotice', component: LobbyNoticeComponent},
            {path: 'inGamingTable', component: InGamingTableComponent},
            {path: '1on1support', component: OneOnOneSupportComponent},
          //log
            {path: 'gameLog', component: GameLogComponent},
            {path: 'jackpotLog', component: JackpotLogComponent},
            {path: 'transferLog', component: TransferLogComponent},
            {path: 'pointLog', component: PointLogComponent},
          //pending
            {path: "dashboard",  component: DashboardComponent},
            {path: "**",  component: DashboardComponent }
        ]
      },

      {path: 'login', component: LoginComponent},
      {path: '', redirectTo: 'admin', pathMatch: 'full'}
  ]

enableProdMode();  
@NgModule({
  declarations: [
    AppComponent,
    // sales
      //office list  
      HeadOfficeListComponent,
      OfficeInfoDialog,
      UserNameCheckSalesBottomSheet,
      // members list
      MemberListComponent,
      MemberInfoDialog,

    CalculateManageComponent,
    UserProfitComponent,
    ConnectingUserComponent,
    BlackListComponent,
    IPinquireComponent,
    DepositManagementComponent,
    WithdrawManagementComponent,
    LobbyNoticeComponent,
    InGamingTableComponent,
    OneOnOneSupportComponent,
    GameLogComponent,
    JackpotLogComponent,
    TransferLogComponent,
    DashboardComponent,
    PointLogComponent,
    LoginComponent,
    MasterPageComponent,
    TranslateDirective,
    NoResultBottomSheet,
    HandHistoryDialog,
    // independent classes
    NoResultBottomSheet2,
    BlackListDialog,
    AnswerSupportDialog,
    SettingsDialog
  ],
  imports: [
    FormsModule,
    ReactiveFormsModule,
    ChartsModule,
    BrowserModule,
    HttpModule,
    RouterModule.forRoot(routes),
    // angular material
    MatSidenavModule,
    BrowserAnimationsModule,
    MatSliderModule,
    MatProgressSpinnerModule,
    MatToolbarModule,
    MatIconModule,
    MatButtonModule,
    MatExpansionModule,
    MatListModule,
    MatGridListModule,
    MatCardModule,
    MatProgressBarModule,
    MatTableModule,
    MatTooltipModule,
    MatDialogModule,
    MatSlideToggleModule,
    MatPaginatorModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatBottomSheetModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatSortModule,
    MatChipsModule,
    MatBadgeModule
  ],
  // exports:[RouterModule],
  entryComponents: [
    //independent classes
    BlackListDialog,
    NoResultBottomSheet2,
    AnswerSupportDialog,
    SettingsDialog,

    NoResultBottomSheet,
    HandHistoryDialog,
    
    // sales
      //office
      OfficeInfoDialog,
      UserNameCheckSalesBottomSheet,
      //members list
      MemberInfoDialog
  ],
  providers: [
    CommonService,
    UserAuthGuard,
    MatDatepickerModule
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
