import { Component, OnInit, Inject } from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {MatBottomSheet, MatDialog, MatDialogRef, MAT_DIALOG_DATA, Sort} from '@angular/material';
import { GameLogService } from '../../../services/log/gameLog/game-log.service';
import { CommonService } from '../../../services/common/common.service'
import { environment } from '../../../../environments/environment.prod';
import {PageEvent} from '@angular/material';
import {GameLogListModel} from '../../../interfaces/GameLogListModel'
import {HandHistoryModel} from '../../../interfaces/HandHistoryModel'


@Component({
  selector: 'app-game-log',
  templateUrl: './game-log.component.html',
  styleUrls: ['./game-log.component.css']
})
export class GameLogComponent implements OnInit {
  
  //service variables
    pageIndex:number = 2
  //table variables
    gameLogList : GameLogListModel[]
    currenHandHistory : HandHistoryModel[]
    currentGameNo : string
    displayedColumns:string[] =[  'row_number',
                                  'ScreenName',
                                  'DateTime',
                                  'GameType',
                                  'Rank',
                                  'BeforePoints',
                                  'WinPoints',
                                  'AfterPoints',
                                  'HandHistory',
                                  'Status'
                                ]
  //angular material search variables
    searchForm:FormGroup;
    searchValue:string;

    tableSort:Sort = {active : 'DateTime', direction : 'desc'}
  // MatPaginator Inputs
    pageSizeOptions: number[] = environment.pageSizeOptions;
  
    // MatPaginator Output
    pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //search variables
    backLoading:boolean = true

  constructor(private commonSrvc:CommonService, 
              private gameLogSrvc:GameLogService, 
              fb:FormBuilder,
              private bottomsheet:MatBottomSheet,
              public dialog: MatDialog) { 

      this.searchForm = fb.group({
        value: null
      });
    
  }
  //lifecycle hooks
    ngOnInit() {
      this.getListAndPageCount()
    }
    ngOnDestroy() {
      console.log('you leave game log')
    }
  //lifecycle hooks end

  getListAndPageCount(){
    Promise.all([this.getGameLogList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getGameLogList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.tableSort.active, this.tableSort.direction)
      .subscribe(
        (result :GameLogListModel[]) => {
          
          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
            let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
            for(let i = 0; i < result.length; i++){
              row_number_new += 1;
              result[i].row_number = row_number_new
            }
            //set artificial index end
            this.gameLogList = result
            this.backLoading = false
          }else{

          }
          resolve()
        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }



  //hand history
  getHandHistory(seasonID:string){

    this.currentGameNo = seasonID
    this.gameLogSrvc.getHandHistory(seasonID)
    .subscribe(
      (result : HandHistoryModel[]) => {
        
        const handHistoryDialog = this.dialog.open(HandHistoryDialog, {
          width: '50%',
          data : result
        });
    
        handHistoryDialog.afterClosed().subscribe(
          () =>{
            console.log('handhistory closed')
          }
        )
        this.currenHandHistory = result
       },
     error => {
         console.log(error['status'])
       }
     )
  }


  searchList(){
    this.commonSrvc.searchList(this.pageIndex,'ScreenName',this.searchValue)
    .subscribe(
      (result:GameLogListModel[]) => {
        
        //if there's result
        if(result.length > 0){
          this.gameLogList = result

          console.log('results found')
        }else{
          console.log('no results found')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          this.openSearchNoResultFound()

          //empty list
          this.gameLogList = []
        }
      }
    )
  }

  openSearchNoResultFound(){
    this.bottomsheet.open(NoResultBottomSheet).afterDismissed().subscribe(
      () => { 
          this.reload()
          this.searchValue = null;
        }
    ) 
  }

  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }

}
@Component({
  selector: 'no-result-bottom-sheet',
  templateUrl: 'no-result-bottom-sheet.html',
})
export class NoResultBottomSheet {
  constructor() {}
}

@Component({
  selector: 'hand-history-dialog',
  templateUrl: 'hand-history-dialog.html',
})
export class HandHistoryDialog {

  constructor(
    public dialogRef: MatDialogRef<HandHistoryDialog>,
    @Inject(MAT_DIALOG_DATA) public data: HandHistoryModel[]) {}

  close(): void {
    this.dialogRef.close();
  }

}