import { Component,Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import { BlackListService } from '../../services/user/blackList/black-list.service'
import { OptionModel } from '../../interfaces/OptionModel'

interface inquireModel{
  ScreenName: string,
  UserAccountID: string,
  UserName: string,
  newStatus: string
}

@Component({
    templateUrl: 'black-list-dialog.html',
  })

export class BlackListDialog {

  // inquire variables
    playeropts:OptionModel[] =[
      {value:'0',name:'ID'},
      {value:'1',name:'Screen Name'}
    ]
    optionValue:string = '1'
    searchValue:string = 'player31'
    
    playerDoesNotExist:boolean = false

  // user info & reason variables
    inquireResult:inquireModel[]
    reason:string = ''

  //block player variables
    isAlreadyBlocked:boolean = false
  //dialog variables
    blacklistmode:boolean[] = [false,false,false]
    isLoading:boolean = false

  constructor(
    public dialogRef: MatDialogRef<BlackListDialog>,
    private blacklist:BlackListService,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      this.blacklistmode = this.data.blacklistmode

      if(this.blacklistmode){
        if(this.blacklistmode[1]){
          this.optionValue = '0'
          this.searchValue = this.data.value
          this.userInquire()
        }else{
          console.log('blacklistmode is empty')
        }
      }else{
        console.log('blacklistmode is empty')
      }

  }

  close(): void {
    this.dialogRef.close();
  }

  blacklistUser(){
    if(this.inquireResult[0].UserName,this.reason){
      this.blacklist.blacklistUser(this.inquireResult[0].UserName,this.reason)
      .subscribe(
        result => {
          if(result){
            this.blacklistmode = [false,false,true]
            this.inquireResult[0].newStatus = 'Blocked'
          }
        },
        error => {
          console.log(error['status'])
          if(error['status'] == '404'){
            this.isAlreadyBlocked = true
            alert('not registered user or already in black list ')
          }
        }
      )
    }else{
      console.log('invalid credentials')
    }
  }

  userInquire(){
    this.isLoading = true
    this.blacklist.userInquire2(this.optionValue,this.searchValue)
    .subscribe(
      (result : any)=> {
        this.isLoading = false
        if(result != undefined && result != false ){
          this.inquireResult = result
          this.blacklistmode = [false,true,false]
        }else{
          this.playerDoesNotExist = true
          console.log(result)
        }

      },
      error => {
        console.log(error['status'])
        console.log(error)
        alert('something went wrong')
      }
    )
  }

  releaseUser(){

    this.blacklist.releaseUser(this.inquireResult[0].UserName)
    .subscribe(
      result => {
        console.log(result)
        if(result){
          this.blacklistmode = [false,false,true]
          this.inquireResult[0].newStatus = 'Released'
        }
      },
      error => {
        if(error['status'] == '404'){
          alert('something went wrong')
        }
      }
    )
          
  }

  back(){
    this.searchValue = ''
    this.inquireResult = null
    this.reason = ''
    this.blacklistmode = [true,false,false]
  }

  cancel(){
    this.dialogRef.close();

    this.dialogRef.afterClosed().subscribe(
      ()=>{
        this.searchValue = ''
        this.inquireResult = null
        this.reason = ''
        this.blacklistmode = [false,false,false]
        this.isLoading = false
      }
    )
  }
  test(){
    console.log(this.reason)
  }
}