import { Component, OnInit, LOCALE_ID, Inject } from '@angular/core';
import {CommonService} from '../../../services/common/common.service';
import { environment } from '../../../../environments/environment.prod';
import { LowrankService } from '../../../services/calculate/lowrank/lowrank.service'
import { UserCredentialsService } from '../../../services/user-credentials.service'
import { UserModel } from '../../../interfaces/UserModel'
import { CalculateManageModel } from '../../../interfaces/CalculateManageModel'
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import { FormGroup} from '@angular/forms';
import {PageEvent, Sort} from '@angular/material';
import { formatDate } from '@angular/common';
import { FormsService } from '../../../services/utilities/forms.service'

@Component({
  selector: 'app-calculate-manage',
  templateUrl: './calculate-manage.component.html',
  styleUrls: ['./calculate-manage.component.css']
})
export class CalculateManageComponent implements OnInit {

  constructor(private commonSrvc:CommonService, 
              private lowrankSrvc:LowrankService, 
              private userCredentialsSrvc:UserCredentialsService,
              private formsSrvc:FormsService,
              @Inject(LOCALE_ID) private locale: string) { 
  }

  // lowrank uprank variables
    // we use this if the user goes back
    maxUpstep:number
    commissionDefaultValue:number
    //commission's max value after user select its low rank and default is user's commission
    protected currentParentID:string 
    protected currentCommission:number

    //key to html elements
    protected key:boolean[] = [
      false,//distributor
      false,//headoffice
      false//operating
    ]

    protected parentIDHistory:string[] =[]
  //service variables
    pageIndex:number = 1
  //table variables
    userCredentials:UserModel
    CalculateManageList:CalculateManageModel[] = []

    currentLowRank:number = 1

    displayedColumns:string[] =[  
      'row_number',
      'UserName',
      'Commission',
      'deposit',
      'depositTransfer',
      'withdraw',
      'withdrawTransfer',
      'bettingAmount',
      'rake',
      'userHoldingMoney',
      'profit',
      'hoMoney', // operating head office view only
      'distributorsMoney', // operating head office view only
      'LowrankProfit', //on distributor index only pageIndex = 1
      'Lowrank'
    ]

    tableSort:Sort = {active : 'userHoldingMoney', direction : 'desc'}

  //angular material search variables
    searchForm:FormGroup;
  //search variables
    showBackButton:boolean = false
    backLoading:boolean = true

    timeopts:any[]
    // date
      startDate:string;
      endDate:string;

    // time
      startTime:string;
      endTime:string;

  // MatPaginator
    // input
    pageSizeOptions: number[] = environment.pageSizeOptions;

    // Output
    pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};


//lifecycle hooks
  ngOnInit() {

    if(!this.userCredentials){
      console.log('requested... ')
      this.getUserCredentials()
    }
    this.timeopts = this.formsSrvc.timeOptions
  }
  ngOnDestroy() {
    console.log('you leave calculate manage')
  }

//lifecycle hooks end

// get user credentials for validation
  getUserCredentials(){
    this.userCredentialsSrvc.getPrivileges(localStorage.getItem(environment.tokenStorageKey))
      .subscribe(
        (res:UserModel) => {
          console.log(res)
            this.userCredentials = res
            this.currentCommission = res.Commission
            this.currentParentID = res.UserAccountID
            this.parentIDHistory.push(res.UserAccountID)
            let level;
            if(res.AccountType == "OperatingHeadOffice"){
              level = 3;
              this.maxUpstep = 2
              this.pageIndex = 2
            }else if(res.AccountType == "HeadOffice"){
              level = 2;
              this.maxUpstep = 1
              this.pageIndex = 1
            }else if(res.AccountType == "Distributor"){
              level = 1;
              this.maxUpstep = 0
              this.pageIndex = 0
            }else{
              // should be logged out because shop or edited account type is not allowed here
            }

            for(let i = 0; i < level; i++){
              this.key[i] = true
              console.log('i : ' + i)
            }

            this.getListAndPageCount()
            console.log(this.key)
        }
      )
  }
// end get user credentials for validation

getListAndPageCount(){
  Promise.all([this.getLowrankList(),this.getPageCount()]).then(function() {
    console.log('get list and page count successful');
    }, function(){ //if promise or promise2 fail
    console.log('something went wrong')
  })
}

// pageindex:number, offset:number, limit:number, order:string, direction:string, office:string 
getLowrankList(){
  let promise = new Promise((resolve,reject) => {

    this.lowrankSrvc.getLowrank2(this.pageIndex, 
                                this.pageEvent.pageIndex * this.pageEvent.pageSize, //ofset
                                this.pageEvent.pageSize, // limit
                                this.tableSort.active, // order by
                                this.tableSort.direction, // asc / desc
                                this.currentParentID)
    .subscribe(
      (result:CalculateManageModel[]) => {

        if(result.length > 0){
          //set artificial index
          let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
          for(let i = 0; i <= result.length - 1; i++){
            row_number_new += 1;
            result[i].row_number = row_number_new
          }
          //set artificial index end
          this.CalculateManageList = result
          this.backLoading = false
        }else{
          //result = new LowRank
        }
        resolve()
      },
      error => {
        console.log(error)
        reject()
      }
    )

  })

  return promise;
}

getPageCount(){
  let promise = new Promise ((resolve,reject) => {
    this.lowrankSrvc.getPageCount(this.pageIndex, this.currentParentID)
    .subscribe(
      (result) => {
        this.pageEvent.length = result[0]['ID']
        resolve()
      },
        error => {
          console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
          console.log(error);
          reject()
        }
    )
  })
  return promise;
}


searchList(){

    this.lowrankSrvc.search(this.pageIndex,
                            this.currentParentID,
                            this.startDate + this.startTime,
                            this.endDate + this.endTime)
    .subscribe(
      (result:CalculateManageModel[]) => {
        console.log(result)
        //if there's result
        if(result.length > 0){
          this.CalculateManageList = result

          console.log('results found')
        }else{
          console.log('no result found')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          //empty list
          this.CalculateManageList = []
        }
      }
    )
    this.showBackButton = true
}

back(){
  this.backLoading = true
}

  lowrank(office){
    // in case user force altered the pageIndex, the set it to 0 
    console.log('pageindex : '+this.pageIndex)
    if(this.pageIndex != 0){ 
      
      this.parentIDHistory.push(this.currentParentID)
      this.pageIndex -= 1;
      this.currentParentID = office;
      this.CalculateManageList = [] 
      this.backLoading = true
      
    }else{
      this.pageIndex = 0
    }

    this.reload()
  }

  uprank(){
    // this will keep pageindex to not step up to its limit.
    if(this.maxUpstep != this.pageIndex){
      this.pageIndex += 1;
      this.currentParentID = this.parentIDHistory.pop();
      this.CalculateManageList = [] 
      this.reload()
    }else{
      console.log('invalid uprank')
    }

  }
  
  //we call this function when the user interact with paginator or clicked the refresh button
  reload(){
    this.backLoading = true; 
    this.getListAndPageCount()
  }
  
  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()
    }
  }

  dateValueChanged(event: MatDatepickerInputEvent<Date>){
    if(event.targetElement.id == 'from'){
      this.startDate = formatDate(event.value,'yyyy-MM-dd', this.locale).toString()
    }else if(event.targetElement.id == 'to'){
      this.endDate = formatDate(event.value, 'yyyy-MM-dd',this.locale).toString()
    }else{
      console.log('invalid target ID')
    }

  }

  test(){
    console.log('date start : ' +  this.startDate)
    console.log('time start : ' +  this.startTime)
    console.log('date end : ' + this.endDate)
    console.log('time end : ' + this.endTime)
  }
  
}
