import { Component, OnInit,Inject,LOCALE_ID } from '@angular/core';
import { TransferHistoryModel } from '../../../interfaces/TransferHistoryModel'
import { CommonService } from '../../../services/common/common.service'
import { environment } from '../../../../environments/environment.prod';
import {MatDatepickerInputEvent} from '@angular/material/datepicker';
import {FormBuilder, FormGroup} from '@angular/forms';
import {OptionModel} from '../../../interfaces/OptionModel'
import { formatDate } from '@angular/common';
import {MatBottomSheet, PageEvent, Sort} from '@angular/material';
import { FormsService } from '../../../services/utilities/forms.service'
import { NoResultBottomSheet2 } from '../../../utilities/NoResultBottomSheet/NoResultBottomSheet2'

@Component({
  selector: 'app-transfer-log',
  templateUrl: './transfer-log.component.html',
  styleUrls: ['./transfer-log.component.css']
})
export class TransferLogComponent implements OnInit {

  constructor(private commonSrvc:CommonService,
              fb:FormBuilder,
              private bottomsheet:MatBottomSheet,
              private formSrvc:FormsService,
              @Inject(LOCALE_ID) private locale: string) { 

    this.searchForm = fb.group({
      value: null
    });  

  }
  //service variables
  pageIndex:number = 8
  //table variables
    transferHistoryList:TransferHistoryModel[]
    displayedColumns:string[] =[  
      'row_number',
      'USender',
      'Sender',
      'UReceiver',
      'Receiver',
      'Amount',
      'BalanceAfterTransfer',
      'Reason',
      'TransferedDateTime'
    ]

    tableSort:Sort = {active : 'TransferedDateTime', direction : 'desc'}
  // MatPaginator
    // input
    pageSizeOptions: number[] = environment.pageSizeOptions;

    // Output
    pageEvent: PageEvent = { pageIndex : 0, pageSize : environment.pageSizeOptions[0], previousPageIndex : 0, length: 0};

  //angular material search variables
    searchForm:FormGroup;
    searchValue:string;

  //search variables
    backLoading:boolean = true
    timeopts:any[]

    fromTimeValue:string;
    toTimeValue:string;

    columnOptions:OptionModel[]= [
      {value : '0', name : 'Sent UserName'}, //USender
      {value : '1', name : 'Sent ScreenName'}, //Sender
      {value : '2', name : 'Received UserName'}, //UReceiver
      {value : '3', name : 'Received Screen Name'} //Receiver
    ]

    columnValue:string = '0'

    fromDateValue:string
    toDateValue:string


  //lifecycle hooks
    ngOnInit() {
      this.timeopts = this.formSrvc.timeOptions
      this.getListAndPageCount()
    }
    ngOnDestroy() {
      console.log('you leave transfer log')
    }

  //lifecycle hooks end


  getListAndPageCount(){
    Promise.all([this.getSupportList(),this.getPageCount()]).then(function() {
      console.log('get list and page count successful');
      }, function(){ //if promise or promise2 fail
      console.log('something went wrong')
    })
  }

  getSupportList(){
    let promise = new Promise((resolve,reject) => {
      this.commonSrvc.getList3(this.pageIndex, this.pageEvent.pageIndex * this.pageEvent.pageSize, this.pageEvent.pageSize, this.tableSort.active, this.tableSort.direction)
      .subscribe(
        (result :TransferHistoryModel[]) => {
          //show No results found if 0 result else dont show
          if(result.length > 0){
            //set artificial index
              let row_number_new = this.pageEvent.pageIndex * this.pageEvent.pageSize
              for(let i = 0; i <= result.length - 1; i++){
                row_number_new += 1;
                result[i].row_number = row_number_new
              }
            //set artificial index end
            this.transferHistoryList = result
            this.backLoading = false
          }else{
          }
          resolve()
        },
        error => {
          console.log(error)
          reject()
        }
      )

    })

    return promise;
  }

  getPageCount(){
    let promise = new Promise ((resolve,reject) => {
      this.commonSrvc.getPageCount(this.pageIndex)
      .subscribe(
        (result) => {
          this.pageEvent.length = result[0]['ID']
          resolve()
        },
          error => {
            console.log('key ' + localStorage.getItem(environment.tokenStorageKey));
            console.log(error);
            reject()
          }
      )
    })
    return promise;
  }

  searchList(){
    this.commonSrvc.searchListWithDate(this.pageIndex,this.columnValue,this.searchValue,this.fromDateValue + this.fromTimeValue,this.toDateValue + this.toTimeValue)
    .subscribe(
      (result:TransferHistoryModel[]) => {
        console.log(result)
        //if there's result
        if(result.length > 0){
          this.transferHistoryList = result

          console.log('results found')
        }else{
          console.log('no results found')
        }
      },
      error => {
        if(error['statusText'] == 'Not Found'){
          //empty list
          this.transferHistoryList = []

          this.openSearchNoResultFound()
        }
      }
    )
  }

  // reload() is called after the bottomsheet "no results found" is dismissed
  reload(){
    this.backLoading = true
    this.getListAndPageCount()
  }

  sortData(sort: Sort){
    this.tableSort = { active : sort.active, direction : sort.direction}
    if(this.tableSort.direction){
      this.reload()

    }
  }

  dateValue(event: MatDatepickerInputEvent<Date>){

    if(event.targetElement.id == 'from'){
      // console.log(formatDate(event.value,'yyyy-MM-dd', this.locale))
      this.fromDateValue = formatDate(event.value,'yyyy-MM-dd', this.locale)
			console.log('​TransferLogComponent -> dateValue -> fromDateValue', this.fromDateValue)
    }else if(event.targetElement.id == 'to'){
      this.toDateValue = formatDate(event.value, 'yyyy-MM-dd',this.locale)
			console.log('​TransferLogComponent -> dateValue -> toDateValue', this.toDateValue)
    }else{
      console.log('invalid target ID')
    }

  }
  
  openSearchNoResultFound(){
    this.bottomsheet.open(NoResultBottomSheet2,{
      data : {  value : this.searchValue, 
                column : this.columnValue, 
                fromDate : this.fromDateValue,
                fromTime : this.fromTimeValue,
                toDate : this.toDateValue,
                toTime : this.toTimeValue 
              }
    })
      .afterDismissed().subscribe(
        () => { 
            console.log('dismissed');
            this.reload()
            this.searchValue = null;
          }
      ) 
  }

}
