import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { map } from "rxjs/operators";
import { environment } from '../../../../environments/environment.prod';
import {UserAuthService} from '../../UserAuth/user-auth.service'
@Injectable({
  providedIn: 'root'
})
export class LowrankService {

//http request variables
  page:string[] = [
    '3', //0
    '2', //1
    '1' //2
  ]

  constructor(private http:Http, private userAuthSrvc:UserAuthService) { }
  
  getLowrank(offset:number, office:string, pageindex:number){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/CalculateManage/LowRank/'+this.page[pageindex]
                                            +'/Office/'+office+'/Limit/'+environment.resultLimit+'/Offset/'+offset+'/', this.userAuthSrvc.httpOption)
    .pipe(
      map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }
  // pageIndex:number, offset:number, limit:number, orderBy:string, direction:string
  getLowrank2(pageindex:number, offset:number, limit:number, order:string, direction:string, office:string ){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/CalculateManage/LowRank/'+this.page[pageindex]
                                            +'/Office/'+office+'/Limit/'+limit+'/Offset/'+offset
                                            +'/Order/'+order+'/Direction/'+direction, this.userAuthSrvc.httpOption)
    .pipe(
      map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }
  
  
  getPageCount(pageindex:number, office:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/CalculateManage/Pagination/'+this.page[pageindex]+'/Office/'+office, this.userAuthSrvc.httpOption)
    .pipe(
      map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
    }
    
  search(pageindex:number, office:string, startDateTime:string, endDateTime:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/CalculateManage/Search/LowRank/'+this.page[pageindex]
                                            +'/Office/'+office+'/StartDateTime/'+startDateTime
                                            +'/EndDateTime/'+endDateTime, this.userAuthSrvc.httpOption)
      .pipe(
        map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {})
      )
  }

  get getToken(){
    return localStorage.getItem(environment.tokenStorageKey);
  }

  

}
