import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from "rxjs/operators";
import { environment } from '../../../environments/environment.prod';
// import { environment } from '../../../environments/environment'
import { Router } from '@angular/router';
import { UserAuthService } from '../../services/UserAuth/user-auth.service'

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor(private http:Http, private router:Router, private userAuthSrvc:UserAuthService) { }

  //user active/inactive variables
    private userIsActive:boolean = true

  //http request variables
  page:string[] = [
    'DepositList',//0
    'BlackList',//1
    'GameLogList',//2
    'IPList',//3
    'OneOnOne',//4
    'MembersList',//5
    'WithdrawHistoryList',//6
    'UserProfit',//7
    'TransferHistoryList'//8
  ]


  logout(){
    localStorage.clear()
    this.userAuthSrvc.setLoggedIn(false)
    this.router.navigate(['/login'])
  }
  
  //for http request every time, if user is not active userIsActive = false
    setUserActive(value:boolean){
      this.userIsActive = value
    }

    get userActive(){
      return this.userIsActive
    }


  getList(pageIndex:number, offset:number){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Limit/'+environment.resultLimit+'/Offset/'+offset+'/', this.userAuthSrvc.httpOption)
      .pipe(
        map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
  }
  
  getList2(pageIndex:number, offset:number, limit:number){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Limit/'+limit+'/Offset/'+offset+'/', this.userAuthSrvc.httpOption)
    .pipe(
      map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
    }
    
  getList3(pageIndex:number, offset:number, limit:number, orderBy:string, direction:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Limit/'+limit+'/Offset/'+offset+'/Order/'+orderBy+'/Direction/'+direction, this.userAuthSrvc.httpOption)
    .pipe(
      map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
    }
      
  getList4(pageIndex:number, offset:number, token:string){
      return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Limit/'+environment.resultLimit+'/Offset/'+offset+'/',this.userAuthSrvc.httpOption)
      .pipe(
        map( res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
      )
  }

  getPageCount(pageIndex:number){
    console.log('page count request...')

    return this.http.get(this.userAuthSrvc.server + '/Api/v1/Pagination/'+pageIndex, this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }

  searchList(pageIndex:number,column:string, value:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/'+this.page[pageIndex]+'/Search/Column/'+column+'/Value/'+value, this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }

  searchListWithDate(pageIndex:number,column:string, value:string, startDate:string, endDate:string){
    return this.http.get(this.userAuthSrvc.server + 
                        '/Api/v1/'+this.page[pageIndex]+'/Search/Column/'+column+'/Value/'+value+'/StartDate/'+startDate+'/EndDate/'+endDate, 
                        this.userAuthSrvc.httpOption)
    .pipe(
      map(res => res.arrayBuffer().byteLength > 0 ? res.json() : {} )
    )
  }

  checkIfUserNameExist(username:string){
    return this.http.get(this.userAuthSrvc.server + '/Api/v1/UserNameCheck/'+username)
    .pipe(
      map( res => res.json() )
    )
  }

}
